/* NAVBAR */
#logs {
  border: 2px solid gray;
  width: 80px;
  height: 35px;
  color: black;
  background-color: white;
  border-radius: 10px;
  margin-left: 10px;
  text-align: center;
  margin-top: -20px;
  font-size: 20px;
}

.nav-item {
  font-size: 20px;
}

.navbar-brand {
  font-size: 25px;
}

.navbar-toggler-icon {
  background-color: white;
}
