/* Login/Logout Form */
.container {
  width: 90vw;
  margin: 50px auto;
  background-color: gray;
  border-radius: 5px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);

  margin-bottom: 200px;
}

h2 {
  text-align: center;
  margin-bottom: 20px;
  font-size: 45px;
  font-weight: bold;
}

.form-group {
  margin-bottom: 20px;
}

label {
  display: block;
  font-weight: bold;
  font-size: 2vw;
}

input[type="text"],
input[type="email"],
input[type="number"],
input[type="password"] {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  text-align: center;
  margin-left: -10px;
}

input[type="submit"] {
  width: 100%;
  padding: 10px;
  background-color: lightgray;
  color: black;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
  margin-top: 20px;
  height: 50px;
  font-size: 25px;
  margin-left: -10px;
}

input[type="submit"]:hover {
  background-color: black;
}

/* #sign_contact {
  height: 900px;
} */

@media (max-width: 1200px) {
  label {
    font-size: 30px;
  }
}

/* ContactUS Form */



.containers {
  background-color: gray;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  padding: 20px;
  
  display: flex;
  gap: 20px;
  width: 90vw;
  text-align: center;
 justify-content: center;
 margin-top: 100px;
 margin-bottom: 200px;
}

.info-section h2 {
  margin: 0 0 20px;
  font-size: 34px;
}
.contact_container{
  display: flex;
  justify-content: center;
}

.info-section label,
.info-section p {
  margin-bottom: 5px;
  display: block;
  margin-left: 20px;
}

.info-section input,
.info-section textarea {
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 50vw;
}

.info-section button {
  background-color:#ccc;
  color: black;
  border: none;
  border-radius: 5px;
  padding: 10px;
  cursor: pointer;
  width: 250px;
  font-weight: bold;
  font-size: 20px;

}

.info-section button:hover {
  background-color: black;
  color: white;
}

.info-item {
  display: flex;
  margin-top: 60px;
}

.large-icon {
  font-size: 1.5rem;
  margin-top: 20px;
  /* Adjust the size as needed */
}

.large-icons {
  font-size: 3.5rem;
  margin-top: 20px;
  /* Adjust the size as needed */
}

@media (max-width: 1200px) {
  .containers {
    width: 100vw;
    display: flex;
    flex-wrap: wrap;
  }
  .info-section input,
  .info-section textarea {
    width: 60vw;
  }

  .info-section button {
    margin-top: 30px;
    margin-top: 50px;
    margin-left: 70px;
  }

  .info-section {
    margin: auto;
    width: 100vw;
  }
  .containers{
    width: 90vw;
  }
 .info-item{
    font-size: 45px;
  }
}
@media (max-width:500px)
{
  .personal{
    display: none;
  }
  .info-section button{
    margin-left: 0px;
  }
  .info-section input,
  .info-section textarea{
    width: 100%;
    margin-left: -10px;
  }
  
  .info-section button {
    width: 50%;
    margin-left: 40px;
  }
  .containers{
    width: 90vw;
  }
}

/* TODO LIST CSS */


.container_todo {
  text-align: center;
  position: relative;
}

.container_todo::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-image: url("./components/Todos/backimgs.jpg");
  background-size: cover;
  background-position: center;
  opacity: 0.5;
  z-index: -1;
  margin-top: -20px;
}

h1 {
  color: black;
  font-weight: bold;
  margin-top: 20px;
}

#textids2 {
  width: 50%;
  height: 70px;
  border: 6px solid gray;
  text-align: center;
  border-radius: 10px;
  color: black;
  font-size: 25px;
  margin-bottom: 10px;
}

.same {
  width: 100px;
  padding: 20px;
  height: 70px;
  text-align: center;
  justify-content: center;
  border: 6px solid gray;
  border-radius: 5px;
  display: flex;
  align-items: center;
  margin-top: 10px;
}

#id_1 {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-bottom: 30px;
}

#box2,
#box3 {
  width: 50%;
  text-align: center;
  margin-top: -20px;
}

#box2 h2 {
  border: 5px solid black;
  background-color: #ccc;
  border-radius: 5px;
  margin-top: 20px;
  font-weight: bold;
  color: black;
  font-size: 25px;
 width: 50%;
 margin-left: 10px;

}

.task-item {
  border: 5px solid black;
  background-color: #ccc;
  border-radius: 15px;
  margin-top: 20px;
  color: black;
  width: 80%;
  margin-left: 10px;
}

.my-img {
  max-height: 30px;
  max-width: 30px;
  border-radius: 5px;
  margin-left: 10px;
  cursor: pointer;
}

.same {
  margin-left: 20px;
  margin-bottom: 20px;
}

.task-item {
  border-radius: 10px;
  border: 2px solid gray;
  background-color: lightgray;
  color: black;
  margin-top: 10px;

}

@media (min-width: 768px) {

  #id_1 {
    flex-direction: row;
    align-items: center;
  }

  #box2,
  #box3 {
    display: inline-block;
    vertical-align: top;
    width: calc(50% - 20px);
  }

  #box3 {
    margin-top: 20px;
    margin-left: 10px;
  }

  .task-item {

    margin-top: 10px;
    font-size: 230%;

  }
}

@media (max-width: 1000px) {

  #box3 {

    visibility: hidden;
  }

  #box2 h2 {
    margin: auto;
    width: 100vw;
    margin-top: 10px;
    font-size: 200%;

  }

  .task-item {
    margin: auto;
    width: 100vw;
    margin-top: 10px;
    font-size: 220%;

  }
}